import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const state = {
  count: 1
};

const mutations = {
  add(state,n){
    state.count += n;
  },
  reduce(state,n){
    state.count -= n;
  }
};

const getters = {
  aaa(state){
    return  'count值为：' + state.count;
  }
};

const actions = {
  addAction({commit}){
    commit('add',5);
    setTimeout(() => {
      commit('reduce',5);
    },2000);
    console.log('addAction');
  },
  reduceAction({commit}){
    commit('reduce',5);
  }
};

export default new Vuex.Store({
  state,
  mutations,
  getters,
  actions
})



