<template>
  <div id="scanner">
    <div class="model" :style="{ width: viewportWidth + 'px', height: viewportHeight + 'px',borderTopWidth: borderTopSize + 'px',borderBottomWidth: borderBottomSize + 'px' }">
      <div class="scanner-view">
        <div class="scanner-view-arrow arrow1"></div>
        <div class="scanner-view-arrow arrow2"></div>
        <div class="scanner-view-arrow arrow3"></div>
        <div class="scanner-view-arrow arrow4"></div>
        <div class="scanner-line"></div>
      </div>
      <div class="scanner-text">放入框内，自动扫描</div>
    </div>
    <video class="video-view" :style="{ height: viewportHeight + 'px' }" ref="video" autoplay playsinline="true" webkit-playsinline="true"></video>
    <canvas ref="canvas" :width="cameraWidth" :height="cameraHeight" style="display: none"></canvas>
    <canvas ref="canvas1" :width="canvas1Width" :height="canvas1Height" style="display: none"></canvas>
    <canvas ref="canvas2" :width="scanWidth" :height="scanHeight" style="display: none"></canvas>
  </div>
</template>

<script>
  import Quagga from 'quagga';
  import jsQR from 'jsqr';
  export default {
    name: 'w-scanner',
    props: {
      // 扫码状态
      scanStatus: {
        default: {
          status: true
        }
      },
      // 视口长宽
      viewportWidth:{
        type: Number
      },
      viewportHeight:{
        type: Number
      },
      // 扫描框长宽
      scanWidth:{
        type: Number
      },
      scanHeight:{
        type: Number
      }
    },
    data() {
      return {
        // 摄像头分辨率
        cameraWidth: 0,
        cameraHeight: 0,
        canvas1Width: 0,
        canvas1Height: 0,
        // 扫描框上下边框
        borderTopSize: 0,
        borderBottomSize: 0,
        // 循环扫码定时器
        scanTimer: null
      }
    },
    methods: {
      handleImageSize(video,item,item1,item2){
        let videoWidth = this.cameraWidth,
            videoHeight = this.cameraHeight,
            viewWidth = this.viewportWidth,
            viewHeight = this.viewportHeight,
            scanW = this.scanWidth,
            scanH = this.scanHeight,
            w,h;
        let context1 = item1.getContext("2d"),
            context2 = item2.getContext("2d"),
            context = item.getContext("2d");
        if (viewWidth/viewHeight > videoWidth/videoHeight){
          // 视口比大于摄像头分辨率比 => 摄像头高度需要裁剪
          w = viewWidth,h = parseInt(w/(videoWidth/videoHeight));
        }else if (viewWidth/viewHeight < videoWidth/videoHeight){
          // 视口比小于摄像头分辨率比 => 摄像头宽度需要裁剪
          h = viewHeight,w = parseInt(h*(videoWidth/videoHeight));
        }else {
          // 视口比等于摄像头分辨率比 => 摄像头长宽都不需要裁剪
          w = viewWidth,h = viewHeight;
        }
        this.canvas1Width = w;
        this.canvas1Height = h;
        let getPixelRatio = function(context) {
          let backingStore = context.backingStorePixelRatio ||
              context.webkitBackingStorePixelRatio ||
              context.mozBackingStorePixelRatio ||
              context.msBackingStorePixelRatio ||
              context.oBackingStorePixelRatio ||
              context.backingStorePixelRatio || 1;

          return (window.devicePixelRatio || 1) / backingStore;
        };
        this.$nextTick(() => {
          let ratio = getPixelRatio(context1);
          console.log(ratio);
          item.width = videoWidth*ratio;
          item.height = videoHeight*ratio;
          context.drawImage(video,0,0,videoWidth,videoHeight,0,0,videoWidth*ratio,videoHeight*ratio);
          item.style.width = videoWidth + 'px';
          item.style.height = videoHeight + 'px';

          item1.width = this.canvas1Width*ratio;
          item1.height = this.canvas1Height*ratio;
          context1.drawImage(item,0,0,videoWidth*ratio,videoHeight*ratio,0,0,w*ratio,h*ratio);
          item1.style.width = this.canvas1Width + 'px';
          item1.style.height = this.canvas1Height + 'px';

          item2.width = scanW*ratio;
          item2.height = scanH*ratio;
          context2.drawImage(item1,((w/2)-(scanW/2))*ratio,((h/2)-(scanH/2))*ratio,scanW*ratio,scanH*ratio,0,0,scanW*ratio,scanH*ratio);
          item2.style.width = scanW + 'px';
          item2.style.height = scanH + 'px';
        })
      },
      initVideo(constrains){
        let _this = this;
        if(navigator.mediaDevices.getUserMedia){
          //最新标准API
          navigator.mediaDevices.getUserMedia(constrains).then(_this.videoSuccess).catch(_this.videoError);
        } else if (navigator.webkitGetUserMedia){
          //webkit内核浏览器
          navigator.webkitGetUserMedia(constrains).then(_this.videoSuccess).catch(_this.videoError);
        } else if (navigator.mozGetUserMedia){
          //Firefox浏览器
          navigator.mozGetUserMedia(constrains).then(_this.videoSuccess).catch(_this.videoError);
        } else if (navigator.getUserMedia){
          //旧版API
          navigator.getUserMedia(constrains).then(_this.videoSuccess).catch(_this.videoError);
        }
      },
      videoSuccess(stream){
        let video = this.$refs.video,
            _this = this;
        //将视频流设置为video元素的源
        video.srcObject = stream;
        //播放视频
        video.play();
        video.oncanplay = function () {
          // 摄像头分辨率
          console.log('摄像头分辨率');
          console.log(video.videoWidth + 'x' + video.videoHeight);
          // 视口分辨率
          console.log('视口分辨率');
          console.log(window.innerWidth + 'x' + window.innerHeight);
          // 设备像素比
          console.log('设备像素比');
          console.log(window.devicePixelRatio);
          _this.cameraWidth = video.videoWidth;
          _this.cameraHeight = video.videoHeight;
          // 发送图片进行识别
          _this.readImg();
        };
      },
      videoError(error){
        console.log("访问用户媒体设备失败：",error.name,error.message);
      },
      readImg(){
        let video = this.$refs.video,
            canvas = this.$refs.canvas,
            canvas1 = this.$refs.canvas1,
            context = canvas1.getContext("2d"),
            canvas2 = this.$refs.canvas2,
            context2 = canvas2.getContext("2d"),
            _this = this;
        let getPixelRatio = function(context) {
          let backingStore = context.backingStorePixelRatio ||
              context.webkitBackingStorePixelRatio ||
              context.mozBackingStorePixelRatio ||
              context.msBackingStorePixelRatio ||
              context.oBackingStorePixelRatio ||
              context.backingStorePixelRatio || 1;

          return (window.devicePixelRatio || 1) / backingStore;
        };
        let ratio = getPixelRatio(context2);
        let timer = setInterval(function () {
          _this.handleImageSize(video,canvas,canvas1,canvas2);
        let imgUri = canvas2.toDataURL();
          // 扫码条形码(本地)
           _this.readBarcode(imgUri,timer);

          // 扫码二维码
          let imageData = context2.getImageData(0, 0, _this.scanWidth*ratio, _this.scanHeight*ratio);
          _this.readQrcode(imageData.data,ratio,timer);
        },1000);
        this.scanTimer = timer;
      },
      readBarcode(imgBase64,timer){
        let _this = this;
        Quagga.decodeSingle({
          decoder: {
            readers: ["code_128_reader"]
          },
          // locate为true程序会自动寻找条码，找不到不会出结果。会对一些清晰度不高的图片有影响
          locate: false,
          src: imgBase64
        }, function(result){
          if (result){
            if(result.codeResult){
              // 扫描成功后清除定时器，停止扫描
              clearInterval(timer);
              _this.$emit('ondata',{ type: 'barcode', result:  result.codeResult.code});
            }else {
              console.log("正在扫条形码...not detected");
            }
          }else {
            console.log("正在扫条形码...not detected");
          }
        });
      },
      readQrcode(data,ratio,timer){
        let _this = this;
        let code = jsQR(data, _this.scanWidth*ratio, _this.scanHeight*ratio, {
          // 只支持白底黑码，默认支持白底黑码和黑底白码。
          inversionAttempts: "dontInvert",
        });

        if (code){
          clearInterval(timer);
          _this.$emit('ondata',{ type: 'qrcode', result:  code.data});
          //_this.$refs.audio.play();
        }else {
          console.log('正在扫二维码...');
        }
      },
      resetCanvas(canvas,width,height){
        canvas.width = width;
        canvas.height = height;
      },
      getOrientationChange(){
        return new Promise((resolve, reject) => {
          let supportOrientation = (typeof window.orientation === 'number' &&
              typeof window.onorientationchange === 'object');
          let updateOrientation = () => {
            let orientation;
            if (supportOrientation){
              orientation = window.orientation;
              switch(orientation){
                case 90:
                case -90:
                  orientation = 'landscape';
                  break;
                default:
                  orientation = 'portrait';
                  break;
              }
              resolve(orientation);
            }else {
              orientation = (window.innerWidth > window.innerHeight) ? 'landscape' : 'portrait';
              resolve(orientation);
            }
          };
          if (supportOrientation){
            // 监听屏幕转动
            window.addEventListener('orientationchange',updateOrientation,false);
          }else {
            //监听resize事件
            window.addEventListener('resize',updateOrientation,false);
          }
        })
      }
    },
    watch: {
      'scanStatus.status'(curVal,oldVal){
        console.log(curVal);
        if(curVal){
          // 开始扫码
          this.readImg();
        }else {
          // 暂停扫码
          clearInterval(this.scanTimer);
          // 清除canvas2内容
          this.resetCanvas(this.$refs.canvas2,this.scanWidth,this.scanHeight);
        }
      }
    },
    mounted(){
      // 检测浏览器是否支持getUserMedia
      if (navigator.mediaDevices.getUserMedia || navigator.getUserMedia || navigator.webkitGetUserMedia || navigator.mozGetUserMedia){
        // 调用用户媒体设备，访问摄像头
        this.initVideo({
          video:{
            // 这里*2保证获取到摄像头为高分辨率,以便更好的识别
            width: window.innerWidth*2,
            facingMode: {
              // 强制后置摄像头,pc端做测试请注释掉，不然会报错
              exact: "environment"
            }
          }
        });
      } else {
        alert("你的浏览器不支持访问用户媒体设备");
      }
      // 当检测到屏幕旋转后页面刷新
      this.getOrientationChange()
          .then(result => {
            if (result){
              window.location.reload();
            }
          });
      this.$nextTick(() => {
        // 初始化border
        this.borderTopSize = (window.innerHeight - (window.innerWidth*0.7))/2;
        this.borderBottomSize = (window.innerHeight - (window.innerWidth*0.7))/2;
      });
    }
  }
</script>


<style scoped>

  /* 右侧扫码 */
  #scanner {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    position: relative;
  }
  .model{
    box-sizing: border-box;
    width: 50vw;
    /*height: 100vh;*/
    position: relative;
    z-index: 88;
    /*border-top: calc((698px - 30vw)/2) solid rgba(0,0,0,.4);*/
    /*border-bottom: calc((698px - 30vw)/2) solid rgba(0,0,0,.4);*/
    border-top-style: solid;
    border-top-color: rgba(0,0,0,.4);
    border-bottom-style: solid;
    border-bottom-color: rgba(0,0,0,.4);
    border-right: 15vw solid rgba(0,0,0,.4);
    border-left: 15vw solid rgba(0,0,0,.4);
  }
  .scanner-view{
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    position: relative;
    border: 1px solid rgba(255,255,255,.3);
    z-index: 89;
  }
  .scanner-line{
    position: absolute;
    width: 100%;
    height: 1px;
    background: #49FF46;
    /*background: radial-gradient(ellipse,#71e52c,#001800);*/
    border-radius: 20px;
    z-index: 90;
    animation: myScan 6s infinite;
  }
  @keyframes  myScan{
    0% {
      top: 0;
    }
    50% {
      top: 70vw;
    }

    100% {
      top: 0;
    }
  }
  .scanner-view-arrow{
    position: absolute;
    width: 5vw;
    height: 5vw;
    border: 2px solid #09bb07;
  }
  .scanner-view-arrow.arrow1{
    top: -1px;
    left: -1px;
    z-index: 99;
    border-right: none;
    border-bottom: none;
  }
  .scanner-view-arrow.arrow2{
    top: -1px;
    right: -1px;
    z-index: 99;
    border-left: none;
    border-bottom: none;
  }
  .scanner-view-arrow.arrow3{
    bottom: -1px;
    left: -1px;
    z-index: 99;
    border-right: none;
    border-top: none;
  }
  .scanner-view-arrow.arrow4{
    bottom: -1px;
    right: -1px;
    z-index: 99;
    border-left: none;
    border-top: none;
  }
  .scanner-text {
    color: #909399;
    font-size: 20px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    margin-top: 20px;
  }
  .video-view{
    position: absolute;
    width: 100vw;
    height: 100vh;
    object-fit: cover;
    top: 0;
    left: 0;
    z-index: 80;
  }
</style>



