<template>
  <div>

  </div>
</template>

<script>
  export default {
    name: "Prototype",
    mounted() {
      // 创建对象的方法
      // 1.
      let o1 = { name: 'o1' };
      console.log(o1);
      // 2.
      let o2 = new Object({ name: 'o2' });
      console.log(o2);
      // 3.
      let M = function(name) {
        this.name = name;
      };
      let o3 = new M('o3');
      console.log(o3);
      // 4.
      let p = { name: 'p' };
      let o4 = Object.create(p);
      console.log(o4);

      // 模拟new

    }
  }
</script>

<style scoped>

</style>



