<template>
  <div class="horizontal">
    <slot></slot>
  </div>
</template>

<script>
  export default {
    name: 'w-radio-group',
    data() {
      return {}
    },
    mounted(){
      let _this = this;
      this.$on('checked',(e) => {
        console.log(e);
        _this.$children[0].radioChecked = e;
        _this.$children[1].radioChecked = e;
        _this.$emit('change', e);
        _this.$emit('input', e);
      });
    },
    watch: {
      value(curVal,oldVal){
        if (!curVal){
          this.$children[0].radioChecked = ''
        }
      }
    }
  }
</script>


<style scoped>
  .horizontal{
    display: flex;
    justify-content: center;
    align-items: center;
  }
</style>


