<template>
  <div class="page">
    <div class="page-total">
      总共<span class="all-data">{{count}}</span>条数据，共<span class="all-page">{{allPages}}</span>页
    </div>
    <ul class="page-group">
      <li class="page-item"
          v-show="currentPage>1"
          @click="jumpPage(1)">首页</li>
      <li :class="['page-item',{'page-disable':currentPage == 1}]"
          v-show="currentPage>1"
          @click="prev"><</li>
      <li v-for="num in pageIndexs"
          :class="['page-item',{'page-active':currentPage==num}]"
          @click="jumpPage(num)">{{ num }}</li>

      <li :class="['page-item',{'page-disable':currentPage == allPages}]"
          v-show="!(currentPage === allPages)"
          @click="next">></li>
      <li class="page-item"
          v-show="!(currentPage === allPages)"
          @click="jumpPage(allPages)">末页</li>
    </ul>
    <div class="page-go">
      <span>转到</span>
      <input class="page-ipt" type="text" v-model.number="changePage" />
      <button class="page-btn" @click="jumpPage(changePage)">GO</button>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'w-pagination',
    props: {
      // 当前页
      cPage: Number,
      // 数据总数
      count: Number,
      // 每页显示个数
      pageSize: Number,
      // 中间显示页数
      pPages: Number
    },
    data() {
      return {
        // 当前页
        currentPage: this.cPage,
        // 跳转页
        changePage: '',
        // 中间显示的个数
        perPages: this.pPages
      }
    },
    mounted(){
      this.$emit('change', this.currentPage);
    },
    watch: {
      cPage(cur,old){
        this.currentPage = cur;
      }
    },
    methods: {
      jumpPage: function(id) {
        console.log(id);
        if (parseInt(id) <= this.allPages && parseInt(id) >= 1){
          this.currentPage = parseInt(id);
          this.$emit('change', this.currentPage);
          this.changePage = '';
        }
      },
      prev(){
        if (this.currentPage > 1){
          this.jumpPage(this.currentPage - 1)
        }
      },
      next(){
        if (this.currentPage < this.allPages){
          this.jumpPage(this.currentPage + 1)
        }
      }
    },
    computed:{
      pageIndexs: function() {
        let left = 1,
            right = this.allPages,
            ar = [];
        if (this.allPages >= this.perPages) {
          if (this.currentPage > 5 && this.currentPage < this.allPages - 4) {
            if (this.perPages % 2){
              left = Number(this.currentPage) - ((this.perPages-1)/2);
              right = Number(this.currentPage) + ((this.perPages-1)/2);
            }else {
              left = Number(this.currentPage) - ((this.perPages/2)-1);
              right = Number(this.currentPage) + ((this.perPages/2));
              console.log(left);
              console.log(right);
            }

          } else {
            if (this.currentPage <= 5) {
              left = 1;
              right = this.perPages;
            } else {
              right = this.allPages;
              left = this.allPages - (this.perPages-1);
            }
          }
        }
        while (left <= right) {
          ar.push(left);
          left++;
        }
        return ar;
      },
      allPages(){
        return Math.ceil(this.count/this.pageSize);
      }
    }
  }
</script>


<style scoped>
  .page{
    color: #606266;
    font-size: 14px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 20px 0;
  }
  .page-total, page-go {
    height: 32px;
    line-height: 32px;
  }
  .page-total span{
    background: #409eff;
    border-radius: 4px;
    color: #fff;
    font-weight: bold;
    margin: 0 4px;
    padding: 2px 4px;
  }
  .page-group,.page-item{
    margin: 0;
    padding: 0;
    list-style: none;
  }
  .page-group{
    display: flex;
    align-items: center;
    justify-content: center;
    color: #409eff;
  }
  .page-item{
    padding: 6px 12px;
    border: 1px #d8d8d8 solid;
    border-right: none;
    font-size: 14px;
    line-height: 1.3em;
    cursor: pointer;
    -moz-user-select: none; /*mozilar*/
    -webkit-user-select: none; /*webkit*/
    -ms-user-select: none; /*IE*/
    user-select: none;
  }
  .page-item:first-child{
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
  }
  .page-item:last-child{
    border-right: 1px #d8d8d8 solid;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
  }
  /* Go */
  .page-go span{
    display: inline-block;
    height: 32px;
    line-height: 32px;
  }
  .page-ipt,.page-btn{
    -webkit-appearance: none;
    align-items: center;
    border: 1px solid transparent;
    border-radius: 4px;
    box-shadow: none;
    display: inline-flex;
    font-size: 1em;
    height: 32px;
    justify-content: flex-start;
    padding: .375em .625em;
    position: relative;
    vertical-align: top;
  }
  .page-ipt{
    box-sizing: border-box;
    border-color: #d8d8d8;
    color: #606266;
    box-shadow: inset 0 1px 2px rgba(10,10,10,.1);
    width: 48px;
    text-align: center;
  }
  .page-ipt:focus,.page-ipt:active{
    border-color: #209cee;
    box-shadow: 0 0 0 0.125em rgba(32,156,238,.25);
  }
  .page-btn{
    box-sizing: border-box;
    background-color: #409eff;
    color: #fff;
    cursor: pointer;
    justify-content: center;
    padding-bottom: calc(.375em - 1px);
    padding-left: .75em;
    padding-right: .75em;
    padding-top: calc(.375em - 1px);
    text-align: center;
    white-space: nowrap;
    width: 40px;
  }
  .page-btn:hover, .page-btn:active {
    background: #409eff;
  }
  .page-btn:focus {
    outline: 0;
  }
  .page-disable{
    cursor: not-allowed!important;
    opacity: .75;
  }
  .page-active{
    cursor: default;
    color: #fff;
    background: #409eff;
    border-color: #409eff;
  }
</style>



