<template>
  <div>
<!--    自定义事件-->
    <div id="ev">
      自定义事件
    </div>
    <div class="odiv" id="odiv">
      <span class="ospan">ospan1</span>
      <span class="ospan">ospan2</span>
      <span class="ospan">ospan3</span>
      <span class="ospan">ospan4</span>
    </div>
  </div>
</template>

<script>
  export default {
    name: "Dom",
    mounted() {
      let eve = new Event('test');
      let ev = document.getElementById('ev');
      ev.addEventListener('test',function () {
        console.log('test dispath')
      });
      ev.dispatchEvent(eve);
      let odiv = document.getElementById('odiv');
      odiv.addEventListener('click',function (e) {
        console.log(e.target);
        console.log(e.currentTarget);
      })
    }
  }
</script>

<style scoped>
.odiv{
  padding: 5px 0;
}
  .ospan{
    display: inline-block;
    margin-right: 10px;
    border: 1px solid #ff3860;
    border-radius: 3px;
    padding: 3px;
    color: #4a4a4a;
    background: #ffffff;
    width: 60px;
    height: 20px;
    line-height: 20px;
    text-align: center;
    cursor: pointer;
  }
  .ospan:hover{
    background: #ff3860;
    color: #ffffff;
  }
</style>



