import Vue from 'vue';
import printBox from '../components/w-print.vue';

let PrintConstructor = Vue.extend(printBox);

let wPrint = (options) => {
  let instance = new PrintConstructor().$mount(document.createElement('div'));
  instance.printInfo = options.printInfo;
  // 把组件添加到当前调用页面中
  let printEle = document.getElementById('print-box');
  if (printEle){
    printEle.parentNode.removeChild(printEle);
    console.log('del');
  }

  document.body.appendChild(instance.$el);
};

export default wPrint;



