<template>
  <div>
    <div class="father">
      <div class="children"></div>
    </div>
    <!--BFC解决垂直方向margin重叠-->
    <div class="father2">
      <p>1</p>
      <!--BFC-->
      <div style="overflow: hidden">
        <p>2</p>
      </div>
      <p>3</p>
    </div>
    <!--BFC解决浮动布局内容溢出-->
    <div class="father3">
      <div class="left">left</div>
      <div class="right">BFC解决浮动布局内容溢出</div>
    </div>
    <!--BFC解决清除浮动-->
    <div class="clearf">
      <div class="children">BFC解决清除浮动</div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "BoxModel"
  }
</script>

<style scoped>
  .father{
    box-sizing: border-box;
    background: #ff3860;
    overflow: hidden;
  }
.father .children{
  box-sizing: border-box;
  height: 100px;
  margin-top: 10px;
  background: #4a4a4a;
}
  .father2{
    background: #ff3860;
    overflow: hidden;
  }
  .father2 p{
    background: #66b1ff;
    margin: 10px auto 30px;
  }
  .father3 .left{
    float: left;
    width: 300px;
    height: 200px;
    background: #22c65b;
  }
  .father3 .right{
    height: 220px;
    background: #8a8a8a;
    /*BFC*/
    overflow: hidden;
  }
  .clearf{
    overflow: hidden;
  }
  .children{
    width: 100%;
    float: left;
    height: 150px;
    background: #000;
    color: #8a8a8a;
  }
</style>



