import Vue from 'vue';
import alert from '../components/w-alert.vue';


let AlertConstructor = Vue.extend(alert);

let wAlert = (options) => {
  let instance = new AlertConstructor().$mount(document.createElement('div'));
  instance.content = typeof(options) == 'object' ? options.content : options;
  instance.boxTop = options.boxTop || '20%';
  instance.title = options.title || '提示';
  instance.showCancelButton = typeof(options.showCancelButton) == 'undefined' ? false : options.showCancelButton;
  instance.showModel = typeof(options.showModel) == 'undefined' ? true : options.showModel;
  instance.confirmButtonText = options.confirmButtonText || '确定';
  instance.cancelButtonText = options.cancelButtonText || '取消';
  instance.callback = function (action) {
    if (action === 'cancel'){
      options.cancelCallback ? options.cancelCallback() : '';
    }
    if (action === 'confirm'){
      options.confirmCallback ? options.confirmCallback() : '';
    }
  };
  // 把组件添加到当前调用页面中
  let alertEle = document.getElementById('alert-box');
  if (alertEle){
    alertEle.parentNode.removeChild(alertEle);
  }
  document.body.appendChild(instance.$el);
  instance.visible = true;

};

export default wAlert;




