<template>
  <div>

  </div>
</template>

<script>
  export default {
    name: "ObjectOriented",
    mounted() {
      // 面向对象
      // 类生命
      function People1() {
        this.name = 'wangyu';
      }
      class People2 {
        constructor() {
          this.name = 'name';
        }
      }
      // 类实例化
      console.log(new People2());
      // 类继承
      // 1. 不能继承父类原型对象上的方法
      function Parent1() {
        this.name = 'Parent1'
      }
      function Child1() {
        People1.call(this);
        this.type = 'child';
      }
      // 2. 由于是原型链对象是共享的，会导致不同的实例对象里面的方法或者属性互通。
      function Parent2() {
        this.name = 'Parent2'
      }
      function Child2() {
        this.type = 'child2';
      }
      Child2.prototype = new Parent2();
      console.log(new Child2().__proto__);
      // 3. 结合以上两种（构造函数+原型）
      function Parent3() {
        this.name = 'Parent3'
      }
      function Child3() {
        Parent3.call(this);
        this.type = 'child3';
      }
      // 不能区分直接创建的构造函数
      // Child3.prototype = Parent3.prototype;
      // 可以区分
      Child3.prototype = Object.create(Parent3.prototype);
      Child3.prototype.constructor = Child3;
      let s1 = new Child3();
      let s2 = new Child3();
      console.log(s1);
    }
  }
</script>

<style scoped>

</style>



