<template>
  <div class="select-box-container">
    <div class="select-box-wrapper" :style="{ width: selectWidth + 'px' }" @click.stop="openOptions">
      <input :style="{ height: selectHeight + 'px'}" ref="ipt2" readonly class="select-box-ipt2" type="text" v-model="visibleText">
      <input :style="{ height: selectHeight + 'px'}" ref="ipt1" readonly :class="['select-box-ipt1',rules ? 'v-validate' : '']" v-validate="rules" type="text" :value="value" @input="$emit('input', $event.target.value)">
      <span :class="['iconfont','icon54','icon-box-arrow',optionVisible ? 'icon-box-arrow-up' : '']"></span>
    </div>
    <ul :class="['select-box-options',isBar ? 'select-bar' : '']" :style="{ width: selectWidth + 'px',top: topValue + 'px',maxHeight: itemsHeight + 'px' }" v-show="optionVisible">
      <li v-for="item in selects" @click="selectedItem(item)">{{item.text}}</li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: 'w-select',
    props: {
      selectItems: {
        type: Array,
        default: []
      },
      value:{
        default: ''
      },
      selectRule:{
        type: Object
      },
      selectWidth:{
        type: Number,
        default: 200
      },
      selectHeight:{
        type: Number,
        default: 34
      },
      // on under可选值
      itemsDirection:{
        type: String,
        default: 'under'
      },
      // 下拉列表固定高度，当备选项总高度大于设置项会出现滚动条
      itemsHeight:{
        type: Number,
        default: 150
      }
    },
    data() {
      return {
        selects: this.selectItems,
        rules: this.selectRule,
        optionVisible: false,
        visibleText: '',
        topValue: this.selectHeight + 1,
        isBar: false
      }
    },
    methods: {
      selectedItem(item){
        this.$refs.ipt1.value = item.value;
        let element = this.$refs.ipt1;
        let evObj = document.createEvent('Event');
        evObj.initEvent('input', true, true);
        element.dispatchEvent(evObj);
        this.visibleText = item.text;
        this.optionVisible = false;
      },
      openOptions(){
        if (this.optionVisible){
          this.optionVisible = false;
        }else {
          // 关闭其他
          this.$parent.$children.forEach((item,index) => {
            if (item._uid !== this._uid &&  typeof(item.optionVisible) !== "undefined"){
              item.optionVisible = false
            }
          });
          this.optionVisible = true;
        }
      }
    },
    mounted(){
      document.body.addEventListener('click',()=>{
        this.optionVisible = false;
      },false);
      // 初始化topValue
      if (this.itemsDirection === 'on'){
        // 判断是否出现bar
        if (this.itemsHeight < ((this.selectItems.length * 28) + 2)){
          this.topValue = -1 * this.itemsHeight;
        }else {
          this.topValue = -1 * ((this.selectItems.length * 28) + 3);
        }

      }
      // 初始化isBar
      if (this.itemsHeight < ((this.selectItems.length * 28) + 2)){
        this.isBar = true;
      }
      if (this.value !== null || this.value !== undefined || this.value !== ''){
        this.selects.forEach((item,index) => {
          if (this.value === item.value){
            this.visibleText = item.text;
          }
        })
      }
    },
    watch: {
      value(curVal,oldVal){
        if (curVal === ''){
          this.visibleText = '';
        }
        if (curVal !== null || curVal !== undefined || curVal !== ''){
          setTimeout(()=> {
            this.selects.forEach((item,index) => {
              if (curVal === item.value){
                this.visibleText = item.text;
              }
            })
          },50);
        }
      },
      selectItems(curVal,oldVal){
        // 初始化topValue
        if (this.itemsDirection === 'on'){
          // 判断是否出现bar
          if (this.itemsHeight < ((curVal.length * 28) + 2)){
            this.topValue = -1 * this.itemsHeight;
          }else {
            this.topValue = -1 * ((curVal.length * 28) + 3);
          }

        }
        // 初始化isBar
        if (this.itemsHeight < ((curVal.length * 28) + 2)){
          this.isBar = true;
        }
        this.selects = curVal;
      }
    },
  }
</script>


<style scoped>
  
  /* select */
  
  ul,li{
    padding: 0;
    margin: 0;
  }
  .select-box-container{
    position: relative;
  }
  .select-box-wrapper{
    position: relative;
  }
  .select-box-ipt1{
    display: inline-block;
    width: 100%;
    box-sizing: border-box;
    background: #fff;
    border: 1px solid #ccc;
    border-radius: 0;
    -webkit-appearance: none;
    -moz-appearance: none;
    transition: background .5s;
    font-size: 14px;
    padding: 8px;
    outline: none;
    color: transparent;
    position: relative;
    z-index: 10;
  }
  .select-box-ipt2{
    box-sizing: border-box;
    background: none;
    border: none;
    border-radius: 0;
    color: #555;
    padding: 8px;
    font-size: 14px;
    width: 80%;
    outline: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    transition: background .5s;
    box-shadow: none;
    position: absolute;
    z-index: 11;
  }
  .select-box-ipt2:focus + .select-box-ipt1{
    border: 1px solid #aaa;
  }
  .span-icon{
    position: absolute;
    top: 50%;
    right: 15px;
    z-index: 10;
    transform: translateY(-50%);
  }
  .icon-box-arrow {
    position: absolute;
    top: 50%;
    right: 15px;
    z-index: 10;
    color: #bbb;
    font-size: 14px;
    transition: transform .3s;
    transform: translateY(-50%) rotate(0deg);
  }
  
  .icon-box-arrow-up{
    transition: transform .3s;
    transform: translateY(-50%) rotate(180deg);
  }
  
  .select-box-options {
    box-sizing: border-box;
    background-color: #fff;
    border: 1px solid #e4e7ed;
    list-style: none;
    position: absolute;
    left: 0;
    z-index: 13;
    -webkit-box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
    -moz-box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
    box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
  }
  .select-box-options li {
    font-size: 14px;
    color: #606266;
    cursor: pointer;
    padding: 0 8px;
    height: 32px;
    line-height: 32px;
    text-align: left;
    overflow: hidden;
    white-space: nowrap;
  }
  .select-box-options li:hover {
    background: #428bca;
    color: #fff;
  }
  
  /* 滚动条整体部分 */
  .select-bar{
    overflow-y: scroll;
  }
  .select-bar::-webkit-scrollbar {
    width: 12px; /* 纵向滚动条宽度 */
    background-color: #f6f6f6; /* 滚动条整体背景，一般被覆盖着 */
  }
  /* 滚动条里面的滑块 */
  .select-bar::-webkit-scrollbar-thumb{
    border-radius: 8px; /* 滚动条滑块圆角 */
    -webkit-box-shadow: inset 0 0 4px rgba(0,0,0,.3); /* 滚动条滑块阴影 */
    background-color: #eee; /* 滚动条滑块颜色 */
  }
  /* 滚动条的轨道（里面装有Thumb） */
  .select-bar::-webkit-scrollbar-track{
    -webkit-box-shadow: inset 0 0 4px rgba(0,0,0,.2); /* 滚动条轨道阴影 */
    border-radius: 8px; /* 滚动条轨道圆角 */
    background-color: #f6f6f6; /* 滚动条轨道背景 */
  }
</style>


