<template>
  <div>
    <section class="test1">
      <aside class="left">left</aside>
      <article class="center">flex布局</article>
      <aside class="right">right</aside>
    </section>
    <section class="test2">
      <aside class="left">left</aside>
      <article class="right">right</article>
      <aside class="center">浮动布局</aside>
    </section>
    <section class="test3">
      <aside class="left">left</aside>
      <article class="center">定位布局</article>
      <aside class="right">right</aside>
    </section>
    <section class="test4">
      <aside class="left">left</aside>
      <article class="center">网格布局</article>
      <aside class="right">right</aside>
    </section>
  </div>
</template>

<script>
  export default {
    name: "pageLayout",
    data() {
      return{

      }
    },
    methods: {

    },
    mounted() {

    }
  }
</script>

<style scoped>
  /*flex布局*/
  .test1{
    width: 100%;
    height: 200px;
    display: flex;
  }
  .test1 .left{
    flex: none;
    width: 300px;
    height: 100%;
    background: #00d1b2;
  }
  .test1 .center{
    flex: auto;
    height: 100%;
    text-align: center;
    background: #66b1ff;
  }
  .test1 .right{
    flex: none;
    width: 300px;
    height: 100%;
    background: #92a0ba;
  }
  /*浮动布局*/
  .test2{
    width: 100%;
    height: 200px;
    float: left;
  }
  .test2 .left{
    float: left;
    width: 300px;
    height: 100%;
    background: #00d1b2;
  }
  .test2 .center{
    height: 100%;
    background: #66b1ff;
    text-align: center;
  }
  .test2 .right{
    float: right;
    width: 300px;
    height: 100%;
    background: #92a0ba;
  }
  /*定位布局*/
  .test3{
    width: 100%;
    height: 200px;
    position: relative;
    margin-top: 200px;
  }
  .test3 .left{
    position: absolute;
    top: 0px;
    left: 0px;
    width: 300px;
    height: 100%;
    background: #00d1b2;
  }
  .test3 .center{
    height: 100%;
    margin: 0 300px;
    background: #66b1ff;
    text-align: center;
  }
  .test3 .right{
    position: absolute;
    top: 0px;
    right: 0px;
    width: 300px;
    height: 100%;
    background: #92a0ba;
  }
  /*网格布局*/
  .test4{
    display: grid;
    grid-template-columns: 300px auto 300px;
    grid-template-rows: 200px;
  }
  .test4 .left{
    background: #00d1b2;
  }
  .test4 .center{
    background: #66b1ff;
    text-align: center;
  }
  .test4 .right{
    background: #92a0ba;
  }
</style>



