import Vue from 'vue';
import Router from 'vue-router';
import HelloWorld from '../components/HelloWorld';
import scan from '../components/scan';
import wStore from '../components/w-store';
import Interview from '../components/Interview';
import PageLayout from "../components/Interview/PageLayout";
import BoxModel from "../components/Interview/BoxModel";
import Dom from "../components/Interview/Dom";
import Prototype from "../components/Interview/Prototype";
import ObjectOriented from "../components/Interview/ObjectOriented";
import Communication from "../components/Interview/Communication";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/scan',
      name: 'scan',
      component: scan
    },
    {
      path: '/wstore',
      name: 'wStore',
      component: wStore
    },
    {
      path: '/interview',
      name: 'Interview',
      component: Interview,
      children: [
        {
          path: 'layout',
          component: PageLayout,
        },
        {
          path: 'boxmodel',
          component: BoxModel
        },
        {
          path: 'dom',
          component: Dom
        },
        {
          path: 'prototype',
          component: Prototype
        },
        {
          path: 'objectoriented',
          component: ObjectOriented
        },
        {
          path: 'communication',
          component: Communication
        }
      ]
    }
  ]
})



