<template>
  <div :class="['switch-group',value == 0 ? '' : 'is-checked' ]">
    <div class="switch-lable-left">
      <input ref="switch1" type="text" :value="value" @input="$emit('input', $event.target.value)" style="display: none">
    </div>
    <span class="switch-core" @click="switchStatus"></span>
    <span class="switch-lable-right">{{currentText}}</span>
  </div>
</template>

<script>
  export default {
    name: 'w-switch',
    props: {
      value: {
        default: 0
      },
      text: {
        type: Array,
        default: ['NO','YES']
      }
    },
    data() {
      return {
        currentFlag: false,
        currentText: this.text[parseInt(this.value)]
      }
    },
    methods: {
      switchStatus(){
        if (this.value == 0){
          this.currentFlag = true;
          this.currentText = this.text[1];

          this.$refs.switch1.value = 1;
          let element = this.$refs.switch1;
          let evObj = document.createEvent('Event');
          evObj.initEvent('input', true, true);
          element.dispatchEvent(evObj);
        }else {
          this.currentFlag = false;
          this.currentText = this.text[0];

          this.$refs.switch1.value = 0;
          let element = this.$refs.switch1;
          let evObj = document.createEvent('Event');
          evObj.initEvent('input', true, true);
          element.dispatchEvent(evObj);
        }
      }
    }
  }
</script>


<style scoped>
  /* switch开关 */
  .switch-group {
    color: #909399;
    display: inline-flex;
    align-items: center;
    position: relative;
    font-size: 14px;
    line-height: 32px;
    height: 32px;
    vertical-align: middle;
  }
  .switch-lable-left {
    display: inline-block;
    margin-right: 8px;
  }
  .switch-lable-right {
    margin-left: 4px;
  }
  .switch-core {
    margin: 0;
    display: inline-block;
    position: relative;
    width: 40px;
    height: 20px;
    border: 1px solid #dcdfe6;
    outline: none;
    border-radius: 10px;
    box-sizing: border-box;
    background: #dcdfe6;
    cursor: pointer;
    transition: border-color .3s,background-color .3s;
    vertical-align: middle;
  }
  .switch-core:after {
    content: "";
    position: absolute;
    top: 1px;
    left: 1px;
    border-radius: 100%;
    transition: all .3s;
    width: 16px;
    height: 16px;
    background-color: #fff;
  }
  /* switch开关 is-checked 状态 */
  .switch-group.is-checked .switch-core{
    border-color: #409eff;
    background-color: #409eff;
  }
  .switch-group.is-checked .switch-core:after {
    left: 100%;
    margin-left: -17px;
  }
</style>



