<template>
  <div style="padding: 10px">
    <div>
      <div class="com-title">1. 单选组件(w-radio)</div>
      <div style="width: 100px">
        <w-radio-group v-model="wRadioResult">
          <w-radio>是</w-radio>
          <w-radio>否</w-radio>
        </w-radio-group>
      </div>
    </div>
    <div>
      <div class="com-title">2. 开关组件(w-switch)</div>
      是否正确 <w-switch v-model="wSwitchResult1" :text="wSwitchResult2"></w-switch>
    </div>
    <div>
      <div class="com-title">3. 下拉列表组件(w-select)</div>
      <w-select v-model="wSelectResult1" :selectItems="wSelectResult2"></w-select>
    </div>
    <div>
      <div class="com-title">4. 消息提醒(w-toast)</div>
      <button class="button is-primary" @click="getToast">点击提醒</button>
    </div>
    <div>
      <div class="com-title">5. 加载中组件(w-loading)</div>
      <button class="button is-primary" @click="showLoading">显示</button>
      <button class="button" @click="hideLoading">隐藏</button>
      <w-loading loadingText="加载中" :loadingFlag="wLoadingResult"></w-loading>
    </div>
    <div>
      <div class="com-title">6. 消息弹窗编程式(w-alert)</div>
      <button class="button is-primary" @click="getAlert">点击弹窗1</button>
    </div>
    <div>
      <div class="com-title">7. 消息弹窗声明式(w-message-box)</div>
      <button class="button is-primary" @click="getMessageBox">点击弹窗2</button>
      <w-message-box
        boxTitle="请填写你的基本信息"
        :boxVisible="boxVisible"
        :showCancel="showCancel"
        @cancel="cancelBox"
        @confirm="confirmBox">
        <div class="input-div">
          <label for="nike">姓名：</label>
          <input name="nike" type="text" id="nike">
        </div>
        <div class="input-div">
          <label for="age">年龄：</label>
          <input name="age" type="number" id="age">
        </div>
        <div class="input-div">
          <label for="sex">性别：</label>
          <select name="sex" id="sex">
            <option value="0">男</option>
            <option value="1">女</option>
          </select>
        </div>
        <div class="input-div">
          <label for="card">身份证号：</label>
          <input name="card" type="text" id="card">
        </div>
      </w-message-box>
    </div>
    <div>
      <div class="com-title">8. 分页组件(w-pagination)</div>
      <div style="width: 730px">
        <w-pagination
          :cPage="1"
          :count="642"
          :pageSize="10"
          :pPages="5"
          @change="getPage"></w-pagination>
      </div>
    </div>
    <div>
      <div class="com-title">9. 时间日期选择器(w-date-picker)</div>
      <span class="com-title-span">年-月-日-时-分-秒:</span>
      <w-date-picker
        mode="datetime"
        @change="getDate">
      </w-date-picker>
      <span class="com-title-span">年-月-日:</span>
      <w-date-picker
        mode="date"
        @change="getDate">
      </w-date-picker>
      <span class="com-title-span">年-月:</span>
      <w-date-picker
        mode="month"
        @change="getDate">
      </w-date-picker>
      <span class="com-title-span">年:</span>
      <w-date-picker
        mode="year"
        @change="getDate">
      </w-date-picker>
    </div>
    <div>
      <div class="com-title">10. 图片上传(w-upload-img)</div>
      <div>
        <w-upload-img @change="getList"></w-upload-img>
      </div>
    </div>
    <div>
      <div class="com-title">11. 打印组件(w-print)</div>
      <button class="button is-primary" @click="print">点击打印</button>
    </div>
    <div>
      <div class="com-title">12. 扫码组件(w-scanner)</div>
      <button class="button is-primary" @click="goScanner">进入扫描页</button>
    </div>
  </div>
</template>

<script>
  import wDatePicker from './w-date-picker.vue';
  import wRadio from './w-radio/w-radio.vue';
  import wRadioGroup from './w-radio/w-radio-group.vue';
  import wSwitch from './w-switch.vue';
  import wSelect from './w-select';
  import wAlert from "../utils/alert";
  import wToast from "../utils/toast";
  import wLoading from "./w-loading";
  import wPagination from './w-pagination';
  import wUploadImg from './w-upload-img';
  import wPrint from "../utils/print";
  import wMessageBox from './w-message-box';
export default {
  name: 'HelloWorld',
  data () {
    return {
      testVal: '',
      wRadioResult: '',
      wSwitchResult1: 0,
      wSwitchResult2: ['错误','正确'],
      wSelectResult1: '选项一',
      wSelectResult2: [
        {
          text: '选项一',
          value: '选项一'
        },
        {
          text: '选项二',
          value: '选项二'
        },
        {
          text: '选项三',
          value: '选项三'
        },
        {
          text: '选项四',
          value: '选项四'
        }
      ],
      wLoadingResult: false,
      printInfo: [
        {
          value1: '测试内容一',
          value2: '测试内容二',
          value3: '测试内容三',
          value4: '测试内容四',
          value5: '测试内容五',
          value6: '测试内容六',
          value7: '测试内容七',
        }
      ],
      boxVisible: false,
      showCancel: true
    }
  },
  methods:{
    getDate(res){
      console.log(res)
    },
    getToast(){
      wToast({
        duration: 1000,
        position: 'middle',
        message: '这是一个Toast'
      });
    },
    getAlert(){
      wAlert('这是一个toast');
    },
    showLoading(){
      this.wLoadingResult = true;
    },
    hideLoading(){
      this.wLoadingResult = false;
    },
    getMessageBox(){
      this.boxVisible = true;
    },
    getPage(res){
      console.log(res);
    },
    getList(res){
      console.log(res)
    },
    print(){
      wPrint({
        printInfo: this.printInfo
      })
    },
    goScanner(){
      this.$router.push({
        path: 'scan'
      })
    },
    cancelBox(){
      console.log('点击了取消');
      this.boxVisible = false;
    },
    confirmBox(){
      console.log('点击了确定');
      this.boxVisible = false;
    }
  },
  components: {
    wDatePicker,
    wRadio,
    wRadioGroup,
    wSwitch,
    wSelect,
    wLoading,
    wPagination,
    wUploadImg,
    wMessageBox
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .com-title{
    padding: 10px 5px;
    color: #333;
    font-size: 16px;
    font-weight: 800;
    margin-top: 20px;
  }
  .com-title-span{
    display: block;
    padding: 10px 5px;
    color: #333;
    font-size: 14px;
  }
  .button{
    font-size: 14px;
    -webkit-appearance: none;
    outline: 0;
    box-sizing: border-box;
    background-color: #fff;
    border-color: #dbdbdb;
    border-width: 1px;
    color: #363636;
    cursor: pointer;
    justify-content: center;
    padding: 6px 9px;
    text-align: center;
    white-space: nowrap;
    border-radius: 3px;
  }
  .button.is-primary{
    background-color: #409eff;
    border-color: transparent;
    color: #fff;
  }
  .input-div{
    width: 520px;
    margin-top: 30px;
  }
</style>



