<template>
  <div>
    <w-scanner
      :scan-status="scanStatus"
      :viewport-width="viewportWidth"
      :viewport-height="viewportHeight"
      :scan-width="scanWidth"
      :scan-height="scanHeight"
      @ondata="getData"
    ></w-scanner>
  </div>
</template>

<script>
  import wScanner from './w-scanner'
  export default {
    name: "scan",
    data(){
      return{
        scanStatus: false,
        viewportWidth: 0,
        viewportHeight: 0,
        scanWidth: 0,
        scanHeight: 0
      }
    },
    methods: {
      getData(data){
        console.log(data);
        alert(data.result);
      },
      handleSize(){
        this.viewportHeight = window.innerHeight;
        this.viewportWidth = window.innerWidth;
        this.scanWidth = parseInt(window.innerWidth * 0.7);
        this.scanHeight = parseInt(window.innerWidth * 0.7);
      }
    },
    mounted() {
      this.handleSize();
    },
    components: {
      wScanner
    }
  }
</script>

<style scoped>

</style>



