<template>
  <div class="loading-main" v-show="loadingFlag">
    <div class="loading"></div>
    <div class="loading-text">{{loadingText}}</div>
  </div>
</template>

<script>
  export default {
    name: 'w-loading',
    props: ['loadingText','loadingFlag'],
    data() {
      return {}
    }
  }
</script>


<style scoped>
  .loading-main{
    box-sizing: border-box;
    width: 150px;
    padding: 30px;
    background: rgba(0,0,0,.3);
    border-radius: 10px;
    position: fixed;
    top: 50%;
    left: 50%;
    z-index: 110;
    transform: translate(-50%,-50%);
    text-align: center;
  }
  .loading{
    height: 35px;
    width: 35px;
    border-width: 2px;
    border-style: solid;
    border-color: rgb(255, 255, 255) rgb(255, 255, 255) transparent;
    border-radius: 100%;
    background: transparent !important;
    -webkit-animation: c-loading 0.75s 0s infinite linear;
    animation: c-loading 0.75s 0s infinite linear;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    display: inline-block;
  }
  @-webkit-keyframes c-loading
  {
    0%
    {
      -webkit-transform: rotate(0deg) scale(1);
      transform: rotate(0deg) scale(1);
    }
    50%
    {
      -webkit-transform: rotate(180deg) scale(0.8);
      transform: rotate(180deg) scale(0.8);
    }
    100%
    {
      -webkit-transform: rotate(360deg) scale(1);
      transform: rotate(360deg) scale(1);
    }
  }
  
  @keyframes c-loading
  {
    0%
    {
      -webkit-transform: rotate(0deg) scale(1);
      transform: rotate(0deg) scale(1);
    }
    50%
    {
      -webkit-transform: rotate(180deg) scale(0.8);
      transform: rotate(180deg) scale(0.8);
    }
    100%
    {
      -webkit-transform: rotate(360deg) scale(1);
      transform: rotate(360deg) scale(1);
    }
  }
  .loading-text{
    color: #fff;
  }
</style>


